@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&family=Roboto:wght@400&family=Abel&display=swap");
body {
	font-family: "Roboto", sans-serif;
}

.App {
	text-align: center;
	background-color: rgb(199, 229, 255);
	background: #406a84;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

.ui.menu {
	background: rgba(26, 24, 24, 0.66);
	font-family: "Abel", sans-serif;
	box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034), 0 6.7px 5.3px rgba(0, 0, 0, 0.048),
		0 12.5px 10px rgba(0, 0, 0, 0.06), 0 22.3px 17.9px rgba(0, 0, 0, 0.072),
		0 41.8px 33.4px rgba(0, 0, 0, 0.086), 0 100px 80px rgba(0, 0, 0, 0.12);
	z-index: 1000;
}

.ui.menu .item {
	color: #ffffff;
}

.ui.sidebar.menu .item {
	color: #ffffff;
}

.ui.vertical.menu {
	background: rgba(26, 24, 24, 0.66);
	font-family: "Abel", sans-serif;
	box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034), 0 6.7px 5.3px rgba(0, 0, 0, 0.048),
		0 12.5px 10px rgba(0, 0, 0, 0.06), 0 22.3px 17.9px rgba(0, 0, 0, 0.072),
		0 41.8px 33.4px rgba(0, 0, 0, 0.086), 0 100px 80px rgba(0, 0, 0, 0.12);
}

.ui.segment {
	box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034), 0 6.7px 5.3px rgba(0, 0, 0, 0.048),
		0 12.5px 10px rgba(0, 0, 0, 0.06), 0 22.3px 17.9px rgba(0, 0, 0, 0.072),
		0 41.8px 33.4px rgba(0, 0, 0, 0.086), 0 100px 80px rgba(0, 0, 0, 0.12);
}

.ui.button {
	box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034), 0 6.7px 5.3px rgba(0, 0, 0, 0.048),
		0 12.5px 10px rgba(0, 0, 0, 0.06), 0 22.3px 17.9px rgba(0, 0, 0, 0.072),
		0 41.8px 33.4px rgba(0, 0, 0, 0.086), 0 100px 80px rgba(0, 0, 0, 0.12);
}

.line-clamp {
	display: -webkit-box;
	-webkit-line-clamp: 3;
	-webkit-box-orient: vertical;
	overflow: hidden;
}

@media only screen and (max-device-width: 730px) {
	.ui.segment.event-list-pagination {
		background: #c0dffff4;
		position: fixed;
		bottom: 0px;
		left: 0px;
		width: 100%;
		z-index: 1000;
	}

	.ui.button.pagination-button {
		width: 7em;
	}
}

@media only screen and (min-device-width: 730px) {
	.ui.segment.event-list-pagination {
		background: #c0dffff4;
		position: fixed;
		bottom: 0px;
		left: 0px;
		width: 100%;
		z-index: 1000;
	}

	.ui.button.pagination-button {
		width: 8em;
	}
}
